body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
  margin: 0px;
  padding: 0px;
}
a {
    text-decoration: none;   
}

.home__header {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color:#3F9BD1;
    color:#FFFFFF;
    margin-left: 0px;
    margin-right: 0px;
    width: 100vh;
    height: 100px;
    text-decoration: none;
}

.home__add {
    display: inline;
    text-decoration: none;
    font-size: 60px;
    font-weight: bold;
}

.hidden {
    display: none;
}
.faces__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    list-style-type: none;
    justify-content: space-between;
    align-items: center;
} 

.face__element {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    list-style-type: none;
    width: 80px;
    height: 80px;
    border-radius: 40px;
    font-size: 30px;
    font-weight: bold;
    margin: 4px;
}

.happy {
    background-color: #FFCD00;
    color: #4A4A4A;
}

.sad {
    background-color: #FF532C;
    color: #FFFFFF;
}
.form__entire {
    margin: 20px;
}

.form__part {
    border: 0px;
}

.form__info {
    border: 1px solid #AEAEAE;
    border-radius: 5px;
    height: 26px;
    text-align: center;
    color:#AEAEAE;
}

.form__radios{
    display: flex;
    flex-direction: row;
}

.form__input {
    border-radius: 0;
}

.form__input:checked {
    background-color: #3F9BD1;
    color: #FFFFFF;
    border-radius: 0;
}

.form__submit {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    background-color: #3F9BD1;
    margin: 10px 0;
}

.form__submit__button {
    font-size: 30px;
    font-weight: bold;
    text-transform: uppercase;
    color: #F3F8FC;
}

.form__submit__cancel {
    text-transform: capitalize;  
    font-size: 26px;
    font-weight: bold;
    color:#AEAEAE;
}
